import React, { useEffect, useState } from "react";
import { API_URL, API_KEY, IMAGE_BASE_URL } from "../../Config";
import MainImage from "../../views/commons/MainImage";
import GridCards from "../commons/GridCards";
import { Row } from "antd";
import noImg from "../commons/noImg.png";
import { Button } from "../commons/toggleButton";
import { Helmet } from "react-helmet";
import LoadingPage from "../Loading/LoadingPage";
import { useQuery } from "react-query";

function LandingPage() {
  let localValue = localStorage.getItem("themes") === "false" ? false : true;
  const [theme] = useState(localValue);

  const [Movies, setMovies] = useState([]);
  const [MainMovieImage, setMainMovieImage] = useState(null);
  const [CurrentPage, setCurrentPage] = useState(0);

  const fetchMovies = (endpoint) => {
    fetch(endpoint)
      .then((response) => response.json())
      .then((response) => {
        // console.log(response);

        setMovies([...Movies, ...response.results]); //...Movies : ì²˜ìŒ ì¶œë ¥ë˜ëŠ” 20ê°œì˜ ì˜í™”ë¥¼ ë³´ì¡´í•˜ê¸° ìœ„í•¨(ì—†ì„ê²½ìš° ê¸°ì¡´ ì˜í™”ê°€ ì‚¬ë¼ì§€ê³  ìƒˆë¡œìš´ ì˜í™”ê°€ ë‚˜ì˜´)
        setMainMovieImage(response.results[0]);
        setCurrentPage(response.page);
      });
  };

  useEffect(() => {
    //ì¸ê¸°ì˜í™”ë¦¬ìŠ¤íŠ¸ ì¤‘ ì²«í˜ì´ì§€(20ê°œ)ë¥¼ ë°›ì•„ì˜¤ê¸° ìœ„í•œ ê²ƒ
    const endpoint = `${API_URL}movie/popular?api_key=${API_KEY}&page=1`;
    fetchMovies(endpoint);
    // eslint-disable-next-line
  }, []);

  function getMovies() {
    return fetch(`${API_URL}movie/popular?api_key=${API_KEY}&page=1`).then(
      (response) => response.json()
    );
  }

  const { isLoading } = useQuery(["movies"], getMovies);
  // console.log(data, isLoading);

  const loadMoreItems = () => {
    //í•¨ìˆ˜ê°€ ì‹¤í–‰ë˜ë©´ ë‹¤ìŒí˜ì´ì§€(2,3,4,5..)ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ” ê²ƒ
    const endpoint = `${API_URL}movie/popular?api_key=${API_KEY}&page=${
      CurrentPage + 1
    }`;
    fetchMovies(endpoint);
  };

  return isLoading ? (
    <LoadingPage />
  ) : (
    <div style={{ width: "100%", margin: "0" }}>
      <Helmet>
        <title>Home | í™ˆ</title>
      </Helmet>
      {/* mainí˜ì´ì§€ */}
      {MainMovieImage && (
        <MainImage
          image={`${IMAGE_BASE_URL}w1280${MainMovieImage.backdrop_path}`}
          title={MainMovieImage.title}
          text={MainMovieImage.overview}
        />
      )}

      <div style={{ width: "85%", margin: "1rem auto" }}>
        <div style={{ fontSize: "2rem" }}> ìµœì‹ ì˜í™” </div>
        <hr />

        {/* movie grid cards */}
        <Row gutter={[16, 16]}>
          {Movies &&
            Movies.map((movie, index) => (
              <React.Fragment key={index}>
                <GridCards
                  movieList
                  image={
                    movie.poster_path
                      ? `${IMAGE_BASE_URL}w500${movie.poster_path}`
                      : noImg
                  }
                  movieId={movie.id}
                  movieName={movie.title}
                />
              </React.Fragment>
            ))}
        </Row>
      </div>

      <div style={{ display: "flex", justifyContent: "center" }}>
        <Button theme={theme === localValue} onClick={loadMoreItems}>
          Load More
        </Button>
      </div>
    </div>
  );
}

export default LandingPage;
