import Axios from "axios";
import React, { useEffect, useState } from "react";
import { Button } from "../../commons/toggleButton";

function Favorite(props) {
  let localValue = localStorage.getItem("themes") === "false" ? false : true;
  const [theme] = useState(localValue);

  const movieId = props.movieId;
  const userFrom = props.userFrom;
  const movieTitle = props.movieInfo?.title; //ëœë”ë§ë¬¸ì œí•´ê²°
  const moviePost = props.movieInfo?.backdrop_path;
  const movieRunTime = props.movieInfo?.runtime;

  //FavoriteNumber : ì¦ê²¨ì°¾ê¸°í•œ ê²Œì •ì˜ ìˆ˜
  const [FavoriteNumber, setFavoriteNumber] = useState(0);
  const [Favorited, setFavorited] = useState(false);

  let variables = {
    userFrom: userFrom,
    movieId: movieId,
    movieTitle: movieTitle,
    moviePost: moviePost,
    movieRunTime: movieRunTime,
  };

  useEffect(() => {
    Axios.post("/api/favorite/favoriteNumber", variables).then((response) => {
      //   console.log(response.data)
      setFavoriteNumber(response.data.favoriteNumber);

      if (response.data.success) {
      } else {
        alert("ìˆ«ìì •ë³´ë¥¼ ê°€ì ¸ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
      }
    });

    Axios.post("/api/favorite/favorited", variables).then((response) => {
      //   console.log(response.data)

      if (response.data.success) {
        setFavorited(response.data.favorited);
      } else {
        alert("ì •ë³´ë¥¼ ê°€ì ¸ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
      }
    });
  }, [variables]);

  const onClickFavorite = () => {
    if (Favorited) {
      Axios.post("/api/favorite/removeFromFavorite", variables).then(
        (response) => {
          if (response.data.success) {
            setFavoriteNumber(FavoriteNumber - 1);
            setFavorited(!Favorited);
          } else {
            alert("Favorite ë¦¬ìŠ¤íŠ¸ì—ì„œ ì‚­ì œë¥¼ ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
          }
        }
      );
    } else {
      Axios.post("/api/favorite/addToFavorite", variables).then((response) => {
        if (response.data.success) {
          setFavoriteNumber(FavoriteNumber + 1);
          setFavorited(!Favorited);
        } else {
          alert("Favorite ë¦¬ìŠ¤íŠ¸ì—ì„œ ì¶”ê°€ë¥¼ ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
        }
      });
    }
  };

  return (
    <div>
      <Button theme={theme === localValue} onClick={onClickFavorite}>
        {Favorited ? "ì¦ê²¨ì°¾ê¸° ì·¨ì†Œ " : "ì¦ê²¨ì°¾ê¸° ì¶”ê°€ "} {FavoriteNumber}
      </Button>
    </div>
  );
}

export default Favorite;
